import m from 'mithril'
import locales from '../locales/common.toml'
import {
  transition
} from '../../app.js'
import { columnsExitAnim } from './Works/Works'


module.exports = {
  oncreate(v) {
    v.state.lang = window.lang.value
    window.lang.onchange = (e) => {
      console.log(e)
      v.state.lang = window.lang.value
      m.redraw()
    }
  },
  view(v) {
    console.log(locales)
    // const { me, about } = locales['eng'].nav

    return m('#nav.space-between', [
      m('.link-wrapper',
        m('a.home-link.underlined', {
          onclick() {
            if (location.pathname != '/home') {
              transition()
              // columnsExitAnim.direction = 'normal'
              // columnsExitAnim.play()
              setTimeout(() => {
                m.route.set('/')
              }, 1300);
            }
          }
        }, me)),
      m('.link-wrapper',
        m('a.about-link.underlined', {
          onclick() {
            if (location.pathname != '/about') {
              transition()
              columnsExitAnim.direction = 'normal'
              columnsExitAnim.play()
              setTimeout(() => {
                m.route.set('/about')
              }, 1300);
            }
          }
        },
          about)),
      m('.link-wrapper',
        [
          m("select.underlined[name='lang'][id='lang'][form='carform']", {
            onchange(e) {
              localStorage.lang = e.target.value
              location.reload()
            }
          },
            [
              m("option[value='eng']",
                "en"
              ),
              m("option[value='rus']",
                "ru"
              ),
            ]
          )
        ]
      ),
    ])
  }
}
