// import 'src/assets/img/*'
import m from "mithril";
import anime from "animejs";
import Footer from "./Footer";
import Works from './Works/Works';
import { loadEvent } from '../../app'
import { cl } from "./utils";

const Title = {
  view() {
    return m('header.title_root',
      [m(
        ".title-wrapper[data-scroll][data-scroll-repeat]",
        m(
          "section.content__item.content__item--home.content__item--current[data-scroll-speed=2][data-scroll]",
          m(".paragraph-wrapper", m(".content__paragraph[data-scroll]", "Elena Kustova")),
          m(".paragraph-wrapper", m(".content__paragraph[data-scroll]", "Russian")),
          m(".paragraph-wrapper", m(".content__paragraph[data-scroll]", "Photographer")),
          m(".paragraph-wrapper", m(".content__paragraph[data-scroll]", "Based in Italy")),

        ),
      ),
        // m(".divider[data-scroll][data-scroll-repeat]",
        //   m("a.divider-text", "see my work")
        // )
      ])
  }
}

const paragraphsKf = [
  {
    opacity: 0,
    translateY: '200%',
    skew: (_, i) => i % 2 ? '10deg' : '-10deg',
    rotate: (_, i) => i % 2 ? '10deg' : '-10deg',
    duration: 200,
    // delay: ,
    easing: 'easeInOutQuad',
  },
  {
    opacity: 1,
    translateY: '0%',
    skew: '0deg',
    rotate: '0deg',
    duration: 1000,
    easing: 'easeInOutQuad',
  },
]

const completeLoadingAnim = (pars) => anime({
  targets: pars,
  opacity: 0,
  translateY: '-200%',
  skew: (_, i) => i % 2 ? '-10deg' : '10deg',
  rotate: (_, i) => i % 2 ? '-10deg' : '10deg',
  duration: 1500,
  delay: 500,
  easing: 'easeInOutQuad',
  autoplay: false,
  complete() {
    titleAnim('.title_root', '.column', '.nav')
  }
})


const onComplete = () => {
  window.scroller.update()
  document.querySelector('.c-scrollbar')?.classList.remove('hidden')
  window.scroller.start()
}

const columnsAnim = (targets) => anime({
  targets: targets,
  opacity: [0, 1],
  translateY: (_, i) => i == 1 ? ['-50%', 0] : ['50%', 0],
  duration: 1500,
  easing: 'easeOutQuart',
  complete: onComplete
})

const titleAnim = (targets, columns, nav) => anime({
  targets: targets,
  height: '0vh',
  duration: 1000,
  easing: 'easeInOutCubic',
  begin() {
    document.querySelector(nav).classList.remove('visible')
    columnsAnim(columns, nav).play()
  },
})



const waitFor = delay => new Promise(resolve => setTimeout(resolve, delay));

const Home = {
  oncreate() {
    const paragraphs = document.querySelectorAll('.content__paragraph'),
      titleRoot = document.querySelector('.title_root'),
      columns = document.querySelectorAll('.column'),
      nav = document.querySelector('.nav')

    if (!window.scroller) {
      document.body.dispatchEvent(loadEvent({ stop: true }))
      document.querySelector('.c-scrollbar')?.classList.add('hidden')
      anime({
        targets: paragraphs,
        keyframes: paragraphsKf,

      })
    } else {
      titleRoot.style.height = '0vh'
      document.body.dispatchEvent(loadEvent({ stop: true }))
      columnsAnim(columns, nav).play()
    }

  },
  view() {
    return [
      m(Title),
      m(Works),
      m(Footer)
    ]
  }
}

export { completeLoadingAnim }
export default Home
